@mixin filterBrightness() {
    -webkit-filter: brightness(0.2);
    filter: brightness(0.2);
}

@mixin transitionBrightness() {
    -moz-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    -webkit-transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: filter 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

@mixin toolTipHyperLink() {
    display: inline;
    color: $color-purewhite;
    text-decoration: underline;
    text-decoration-thickness: $border-width-hairline;
    text-underline-offset: $border-width-thick;
    transition: text-decoration-thickness $transition-duration
        $transition-function;

    &:visited {
        color: $color-purewhite;
    }

    &:hover,
    &:active {
        text-decoration-thickness: $border-width-default;
    }

    &:focus {
        text-decoration-thickness: $border-width-default;
        outline-color: $color-red50;
    }

    &:before {
        background: initial;
        opacity: initial;
        width: initial;
        height: initial;
    }
}

.wrapper--tooltip__label {
    @include media('>=mobile', '<tablet') {
        display: none;
    }
    @include smalltype();

    margin-top: initial;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('above');
    width: 100%;

    &[data-open='false'] {
        .tooltip__label {
            display: none;
        }
    }

    .tooltip__label {
        display: inline-block;
        margin-left: $measure--basic;
        color: $color-purewhite;
        padding: 0 $measure--half;
        border-left: $border-style-solid $border-width-thick $color-purewhite;
        @include extrasmalltype();

        a {
            @include toolTipHyperLink;
        }
    }

    .tooltip--image-credit {
        @include smalltype();
        background-color: $color-gray10;
        border: 0;
        color: $color-purewhite;
        font-weight: $font-weight-normal;
        margin: 0;
        padding: $measure--half;
        width: auto;
        pointer-events: auto;
        &:hover {
            @include media('hover') {
                background-color: $color-red50;
            }
        }

        &:focus {
            background-color: $color-red50;
        }

        &:active,
        &:focus {
            outline: 0;

            .icon {
                outline: $border-width-default $border-style-solid
                    $color-purewhite;
            }
        }

        .icon {
            min-width: $measure--small3;
            padding: $measure--quarter;
            font-size: $font-size--alpha;
        }
    }
}

.tooltip__overlay {
    @include transitionBrightness();
}

img:has(~ .wrapper--tooltip__label[data-open='true']) {
    @include filterBrightness();
}

.card--news {
    .wrapper--tooltip__label ~ .card__anchor img {
        @include transitionBrightness();
    }

    .wrapper--tooltip__label[data-open='true'] ~ .card__anchor img {
        @include filterBrightness();
    }
}

.card--pathway {
    .wrapper--tooltip__label ~ a img {
        @include transitionBrightness();
    }
    .wrapper--tooltip__label[data-open='true'] ~ a img {
        @include filterBrightness();
    }
}

.news__related-news {
    .card--news {
        .wrapper--tooltip__label {
            margin: $border-width-thick;
            width: calc(100% - #{$border-width-thick} - #{$border-width-thick});
        }
    }
}

.finder__results {
    .card--news {
        .wrapper--tooltip__label {
            margin: $border-width-thicker;
            width: calc(
                100% - #{$border-width-thicker} - #{$border-width-thicker}
            );
        }
    }
}

.pathway {
    .pathway__cards {
        .card--pathway {
            position: relative;
            .wrapper--tooltip__label {
                margin: initial;

                a {
                    border-left: initial;
                    border-right: initial;
                    border-top: initial;
                    background-color: initial;
                    @include toolTipHyperLink;

                    &:active,
                    &:hover,
                    &:focus {
                        background-color: initial;
                    }
                }
            }

            a {
                @include media('hover') {
                    &:hover {
                        .card--pathway__content {
                            background-color: $color-gray1;
                        }
                    }
                }
            }
        }
    }
}
