////
/// Global Header
/// @group patterns
////
.global-header {
    &:not(.global-header--withpromo) {
        @include box-shadow();
    }

    background-color: $color-purewhite;
    position: relative;
    z-index: z-index('unveiled');

    @include media('print') {
        display: none;
    }

    .global-header__content {
        position: relative;

        @include media('<small') {
            align-items: flex-start;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        @include media('>=small') {
            @include standard-box();
            @include standard-grid();

            grid-template-rows: repeat(3, auto);
        }

        @include media('>=desktop') {
            grid-row-gap: $measure--basic;
            grid-template-rows: repeat(2, auto);
        }
    }
}

.header-logo {
    @include media('<small') {
        align-self: center;
        justify-content: flex-start;
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--half;
        padding-top: $measure--half;
        width: $measure--huge5;
    }

    @include media('>=small') {
        grid-column-end: span 3;
        grid-column-start: 1;
        grid-row-end: span 2;
        grid-row-start: 1;
    }

    a {
        @include anchor-img();

        display: inline-block;
        width: 100%;

        @include media('<small') {
            height: $measure--medium7;
        }

        @include media('>=small') {
            margin-bottom: $measure--basic;
            margin-top: $measure--small2;
        }

        .logo {
            width: 100%;

            @include media('<small') {
                height: 100%;
            }
        }
    }
}

.global-header__utility {
    display: none;
    .global-header__utility__sub-menu {
        border: 1px solid $color-gray5;
        padding: 8px 12px;
        border-left: 5px solid $color-brand-primary;
    }
    .dd-menu--open {
        .global-header__utility__link-btn {
            border-color: $color-gray5;
            border-left-color: $color-brand-primary;
        }
    }

    .global-header__utility__link-btn {
        border: 1px solid transparent;
        border-left-width: 5px;
        padding: 10px;
        border-bottom: 0;
        z-index: 2;
        position: relative;
        background: white;

        &:focus {
            @include anchor-default-outline($color-outline);
        }
        &::after {
            //Needed to hide part of the border
            content: '';
            background: white;
            height: 2px;
            display: block;
            position: absolute;
            width: 100%;
            left: 0;
            bottom: -1px;
        }
    }

    .global-header__utility__text {
        color: $color-gray7;
        margin: 0 5px;
    }
    @include media('>=small') {
        display: block;
        grid-column-end: 24;
        grid-column-start: 4;
        grid-row-end: 1;
        grid-row-start: 1;
        margin-top: $measure--small2;
    }

    & > ul {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        height: 100%;
        justify-content: flex-end;

        margin-left: 0;
        margin-top: 0;
        max-width: unset;

        & > li {
            a {
                display: inline-block;
                padding: 10px;
            }
            &:not(:last-of-type) {
                margin-right: $measure--threequarters;
            }
        }
    }

    ul {
        list-style: none;

        li {
            @include smalltype();

            a {
                @include underline-transition--subelement(
                    $color-gray7,
                    $color-gray10,
                    $border-width-thick,
                    $color-nearblack,
                    'global-header__utility__text'
                );

                height: 100%;

                .global-header__utility__icon {
                    color: $color-gray333;
                    margin-left: $measure--quarter;
                    top: -#{$border-width-default};
                }

                &:hover .global-header__utility__icon {
                    @include media('hover') {
                        color: $color-gray10;
                    }
                }
            }
        }
    }
}
.global-header__utility__sub-menu {
    background-color: $color-purewhite;
    z-index: z-index('above');
    width: 150%;
    box-shadow: 0 $measure--half $measure--small1 0 rgba(0, 0, 0, 0.3);
    li {
        border-top: 1px solid $color-gray5;
        &:first-child {
            border-top: none;
            a {
                padding-bottom: 8px;
                padding-top: 5px;
            }
        }
        a {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 8px 0 5px 0;
        }
    }
}
.global-header__controls {
    @include media('<small') {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-end;
        width: 50%;
    }

    @include media('>=small') {
        align-self: stretch;
        display: flex;
        grid-column-end: span 1;
        grid-column-start: 24;
        grid-row-end: 1;
        grid-row-start: 1;
        margin-top: $measure--small2;
    }
}

.navigation--primary {
    display: none;

    @include media('>=small') {
        display: block;
        grid-column-end: span 24;
        grid-column-start: 1;
        grid-row-end: 3;
        grid-row-start: 3;
        padding-bottom: $measure--small2;
    }

    @include media('>=desktop') {
        grid-column-end: span 21;
        grid-column-start: 4;
        grid-row-end: 2;
        grid-row-start: 2;
    }

    > ul {
        align-items: flex-end;
        height: 100%;
    }
}

.announcement {
    background-color: $color-gray10;
    color: $color-purewhite;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;
    position: relative;
    z-index: z-index('inpage');
    width: 100%;

    .announcement__content {
        @include h6style();
        @include standard-box();

        font-weight: $font-weight-normal;
        margin-top: 0;
        text-align: center;

        sup,
        sub {
            font-size: $font-size--super;
            line-height: $font-size--super;
        }

        a {
            @include underline-basic(
                $inactive-color: $color-purewhite,
                $hover-color: $color-purewhite,
                $underline-width: $border-width-default,
                $outline: $color-purewhite
            );
        }
    }
}
